* {
  margin: 0;
  padding: 0;
}

body {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
  font-size: 14px;
}

.form-box {
  height: 50px;
  background-color: #5252ef;
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form {
  color: #333;
  background-color: #f2f2f2;
  border-radius: 5px;
  outline: none;
  align-items: center;
  padding-left: 5px;
  display: flex;
}

.search-form input[type="text"] {
  background-color: #f2f2f2;
  border: none;
}

.search-form input[type="text"]:focus-visible {
  outline: none;
}

.search-form button {
  cursor: pointer;
  background-color: #c6d3f4;
  border: none;
  border-radius: 0 5px 5px 0;
  padding: 4px;
}

.search-form button[type="submit"]:hover {
  background-color: #f2f2f2;
}

.gallery {
  max-width: 100%;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 48px 24px;
  margin-top: 24px;
  display: flex;
}

.photo-card {
  background-color: #eff1f5;
  flex-basis: calc(25% - 48px);
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.photo-card:hover, .photo-card:focus {
  box-shadow: 0 4px 12px rgba(46, 47, 66, .2), 0 2px 4px rgba(46, 47, 66, .12), 0 1px 8px rgba(46, 47, 66, .14);
}

.info {
  justify-content: space-between;
  margin-top: 24px;
  padding: 8px;
  display: flex;
}

.img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.info-item {
  flex-direction: column;
  align-items: center;
  row-gap: 8px;
  display: flex;
}

.load-more {
  cursor: pointer;
  border-radius: 5px;
  margin: 10px;
  padding: 3px;
  display: none;
}

.div-button-load-more {
  justify-content: center;
  display: flex;
}

